@each Rscolor, Rsvalue in Rstheme-colors {
  .link-#{Rscolor} {
    color: Rsvalue;

    @if Rslink-shade-percentage != 0 {
      &:hover,
      &:focus {
        color: if(color-contrast(Rsvalue) == Rscolor-contrast-light, shade-color(Rsvalue, Rslink-shade-percentage), tint-color(Rsvalue, Rslink-shade-percentage));
      }
    }
  }
}
