// Credit: Nicolas Gallagher and SUIT CSS.

.ratio {
  position: relative;
  width: 100%;

  &::before {
    display: block;
    padding-top: var(--#{Rsvariable-prefix}aspect-ratio);
    content: "";
  }

  > * {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}

@each Rskey, Rsratio in Rsaspect-ratios {
  .ratio-#{Rskey} {
    --#{Rsvariable-prefix}aspect-ratio: #{Rsratio};
  }
}
