// Utility generator
// Used to generate utilities & print utilities
@mixin generate-utility(Rsutility, Rsinfix, Rsis-rfs-media-query: false) {
  Rsvalues: map-get(Rsutility, values);

  // If the values are a list or string, convert it into a map
  @if type-of(Rsvalues) == "string" or type-of(nth(Rsvalues, 1)) != "list" {
    Rsvalues: zip(Rsvalues, Rsvalues);
  }

  @each Rskey, Rsvalue in Rsvalues {
    Rsproperties: map-get(Rsutility, property);

    // Multiple properties are possible, for example with vertical or horizontal margins or paddings
    @if type-of(Rsproperties) == "string" {
      Rsproperties: append((), Rsproperties);
    }

    // Use custom class if present
    Rsproperty-class: if(map-has-key(Rsutility, class), map-get(Rsutility, class), nth(Rsproperties, 1));
    Rsproperty-class: if(Rsproperty-class == null, "", Rsproperty-class);

    // State params to generate pseudo-classes
    Rsstate: if(map-has-key(Rsutility, state), map-get(Rsutility, state), ());

    Rsinfix: if(Rsproperty-class == "" and str-slice(Rsinfix, 1, 1) == "-", str-slice(Rsinfix, 2), Rsinfix);

    // Don't prefix if value key is null (eg. with shadow class)
    Rsproperty-class-modifier: if(Rskey, if(Rsproperty-class == "" and Rsinfix == "", "", "-") + Rskey, "");

    @if map-get(Rsutility, rfs) {
      // Inside the media query
      @if Rsis-rfs-media-query {
        Rsval: rfs-value(Rsvalue);

        // Do not render anything if fluid and non fluid values are the same
        Rsvalue: if(Rsval == rfs-fluid-value(Rsvalue), null, Rsval);
      }
      @else {
        Rsvalue: rfs-fluid-value(Rsvalue);
      }
    }

    Rsis-rtl: map-get(Rsutility, rtl);

    @if Rsvalue != null {
      @if Rsis-rtl == false {
        /* rtl:begin:remove */
      }
      .#{Rsproperty-class + Rsinfix + Rsproperty-class-modifier} {
        @each Rsproperty in Rsproperties {
          #{Rsproperty}: Rsvalue if(Rsenable-important-utilities, !important, null);
        }
      }

      @each Rspseudo in Rsstate {
        .#{Rsproperty-class + Rsinfix + Rsproperty-class-modifier}-#{Rspseudo}:#{Rspseudo} {
          @each Rsproperty in Rsproperties {
            #{Rsproperty}: Rsvalue if(Rsenable-important-utilities, !important, null);
          }
        }
      }
      @if Rsis-rtl == false {
        /* rtl:end:remove */
      }
    }
  }
}
