// Shorthand

.fixed-top {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: Rszindex-fixed;
}

.fixed-bottom {
  position: fixed;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: Rszindex-fixed;
}

// Responsive sticky top
@each Rsbreakpoint in map-keys(Rsgrid-breakpoints) {
  @include media-breakpoint-up(Rsbreakpoint) {
    Rsinfix: breakpoint-infix(Rsbreakpoint, Rsgrid-breakpoints);

    .sticky#{Rsinfix}-top {
      position: sticky;
      top: 0;
      z-index: Rszindex-sticky;
    }
  }
}
