// Loop over each breakpoint
@each Rsbreakpoint in map-keys(Rsgrid-breakpoints) {

  // Generate media query if needed
  @include media-breakpoint-up(Rsbreakpoint) {
    Rsinfix: breakpoint-infix(Rsbreakpoint, Rsgrid-breakpoints);

    // Loop over each utility property
    @each Rskey, Rsutility in Rsutilities {
      // The utility can be disabled with `false`, thus check if the utility is a map first
      // Only proceed if responsive media queries are enabled or if it's the base media query
      @if type-of(Rsutility) == "map" and (map-get(Rsutility, responsive) or Rsinfix == "") {
        @include generate-utility(Rsutility, Rsinfix);
      }
    }
  }
}

// RFS rescaling
@media (min-width: Rsrfs-mq-value) {
  @each Rsbreakpoint in map-keys(Rsgrid-breakpoints) {
    Rsinfix: breakpoint-infix(Rsbreakpoint, Rsgrid-breakpoints);

    @if (map-get(Rsgrid-breakpoints, Rsbreakpoint) < Rsrfs-breakpoint) {
      // Loop over each utility property
      @each Rskey, Rsutility in Rsutilities {
        // The utility can be disabled with `false`, thus check if the utility is a map first
        // Only proceed if responsive media queries are enabled or if it's the base media query
        @if type-of(Rsutility) == "map" and map-get(Rsutility, rfs) and (map-get(Rsutility, responsive) or Rsinfix == "") {
          @include generate-utility(Rsutility, Rsinfix, true);
        }
      }
    }
  }
}


// Print utilities
@media print {
  @each Rskey, Rsutility in Rsutilities {
    // The utility can be disabled with `false`, thus check if the utility is a map first
    // Then check if the utility needs print styles
    @if type-of(Rsutility) == "map" and map-get(Rsutility, print) == true {
      @include generate-utility(Rsutility, "-print");
    }
  }
}
