// Breakpoint viewport sizes and media queries.
//
// Breakpoints are defined as a map of (name: minimum width), order from small to large:
//
//    (xs: 0, sm: 576px, md: 768px, lg: 992px, xl: 1200px)
//
// The map defined in the `Rsgrid-breakpoints` global variable is used as the `Rsbreakpoints` argument by default.

// Name of the next breakpoint, or null for the last breakpoint.
//
//    >> breakpoint-next(sm)
//    md
//    >> breakpoint-next(sm, (xs: 0, sm: 576px, md: 768px, lg: 992px, xl: 1200px))
//    md
//    >> breakpoint-next(sm, Rsbreakpoint-names: (xs sm md lg xl))
//    md
@function breakpoint-next(Rsname, Rsbreakpoints: Rsgrid-breakpoints, Rsbreakpoint-names: map-keys(Rsbreakpoints)) {
  Rsn: index(Rsbreakpoint-names, Rsname);
  @if not Rsn {
    @error "breakpoint `#{Rsname}` not found in `#{Rsbreakpoints}`";
  }
  @return if(Rsn < length(Rsbreakpoint-names), nth(Rsbreakpoint-names, Rsn + 1), null);
}

// Minimum breakpoint width. Null for the smallest (first) breakpoint.
//
//    >> breakpoint-min(sm, (xs: 0, sm: 576px, md: 768px, lg: 992px, xl: 1200px))
//    576px
@function breakpoint-min(Rsname, Rsbreakpoints: Rsgrid-breakpoints) {
  Rsmin: map-get(Rsbreakpoints, Rsname);
  @return if(Rsmin != 0, Rsmin, null);
}

// Maximum breakpoint width.
// The maximum value is reduced by 0.02px to work around the limitations of
// `min-` and `max-` prefixes and viewports with fractional widths.
// See https://www.w3.org/TR/mediaqueries-4/#mq-min-max
// Uses 0.02px rather than 0.01px to work around a current rounding bug in Safari.
// See https://bugs.webkit.org/show_bug.cgi?id=178261
//
//    >> breakpoint-max(md, (xs: 0, sm: 576px, md: 768px, lg: 992px, xl: 1200px))
//    767.98px
@function breakpoint-max(Rsname, Rsbreakpoints: Rsgrid-breakpoints) {
  Rsmax: map-get(Rsbreakpoints, Rsname);
  @return if(Rsmax and Rsmax > 0, Rsmax - .02, null);
}

// Returns a blank string if smallest breakpoint, otherwise returns the name with a dash in front.
// Useful for making responsive utilities.
//
//    >> breakpoint-infix(xs, (xs: 0, sm: 576px, md: 768px, lg: 992px, xl: 1200px))
//    ""  (Returns a blank string)
//    >> breakpoint-infix(sm, (xs: 0, sm: 576px, md: 768px, lg: 992px, xl: 1200px))
//    "-sm"
@function breakpoint-infix(Rsname, Rsbreakpoints: Rsgrid-breakpoints) {
  @return if(breakpoint-min(Rsname, Rsbreakpoints) == null, "", "-#{Rsname}");
}

// Media of at least the minimum breakpoint width. No query for the smallest breakpoint.
// Makes the @content apply to the given breakpoint and wider.
@mixin media-breakpoint-up(Rsname, Rsbreakpoints: Rsgrid-breakpoints) {
  Rsmin: breakpoint-min(Rsname, Rsbreakpoints);
  @if Rsmin {
    @media (min-width: Rsmin) {
      @content;
    }
  } @else {
    @content;
  }
}

// Media of at most the maximum breakpoint width. No query for the largest breakpoint.
// Makes the @content apply to the given breakpoint and narrower.
@mixin media-breakpoint-down(Rsname, Rsbreakpoints: Rsgrid-breakpoints) {
  Rsmax: breakpoint-max(Rsname, Rsbreakpoints);
  @if Rsmax {
    @media (max-width: Rsmax) {
      @content;
    }
  } @else {
    @content;
  }
}

// Media that spans multiple breakpoint widths.
// Makes the @content apply between the min and max breakpoints
@mixin media-breakpoint-between(Rslower, Rsupper, Rsbreakpoints: Rsgrid-breakpoints) {
  Rsmin: breakpoint-min(Rslower, Rsbreakpoints);
  Rsmax: breakpoint-max(Rsupper, Rsbreakpoints);

  @if Rsmin != null and Rsmax != null {
    @media (min-width: Rsmin) and (max-width: Rsmax) {
      @content;
    }
  } @else if Rsmax == null {
    @include media-breakpoint-up(Rslower, Rsbreakpoints) {
      @content;
    }
  } @else if Rsmin == null {
    @include media-breakpoint-down(Rsupper, Rsbreakpoints) {
      @content;
    }
  }
}

// Media between the breakpoint's minimum and maximum widths.
// No minimum for the smallest breakpoint, and no maximum for the largest one.
// Makes the @content apply only to the given breakpoint, not viewports any wider or narrower.
@mixin media-breakpoint-only(Rsname, Rsbreakpoints: Rsgrid-breakpoints) {
  Rsmin:  breakpoint-min(Rsname, Rsbreakpoints);
  Rsnext: breakpoint-next(Rsname, Rsbreakpoints);
  Rsmax:  breakpoint-max(Rsnext);

  @if Rsmin != null and Rsmax != null {
    @media (min-width: Rsmin) and (max-width: Rsmax) {
      @content;
    }
  } @else if Rsmax == null {
    @include media-breakpoint-up(Rsname, Rsbreakpoints) {
      @content;
    }
  } @else if Rsmin == null {
    @include media-breakpoint-down(Rsnext, Rsbreakpoints) {
      @content;
    }
  }
}
